<template>
  <div class="product-publish-container">
    <el-card class="box-card">
      <template #header>
        <div class="card-header">
          <h2>商品发布</h2>
        </div>
      </template>
      
      <!-- 添加Coze工作流区域 -->
      <el-collapse class="coze-workflow-section">
        <el-collapse-item title="AI生成商品信息" name="coze">
          <el-alert
            v-if="!hasApiSettings"
            type="warning"
            show-icon
            :closable="false"
            style="margin-bottom: 15px;"
          >
            <p>尚未配置Coze API设置，请先在"设置-API设置"页面中进行配置</p>
            <el-button type="primary" size="small" @click="goToSettings" style="margin-top: 8px;">
              前往设置页面
            </el-button>
          </el-alert>
          
          <el-alert
            v-else
            type="success"
            show-icon
            :closable="false"
            style="margin-bottom: 15px;"
          >
            <p>已成功加载全局API设置</p>
          </el-alert>
          
          <!-- 工作流测试组件 -->
          <CozeWorkflowTester v-if="hasApiSettings" :api-settings="apiSettings" @result="handleWorkflowResult" />
        </el-collapse-item>
      </el-collapse>
      
      <el-form 
        ref="formRef"
        :model="form"
        :rules="rules"
        label-width="120px"
        v-loading="loading"
      >
        <el-form-item label="选择店铺" prop="shop_id">
          <el-select v-model="form.shop_id" placeholder="请选择店铺" @change="fetchShopAuthorize">
            <el-option
              v-for="shop in shops"
              :key="shop.id"
              :label="shop.shop_name"
              :value="shop.id"
            />
          </el-select>
        </el-form-item>
        
        <el-form-item label="商品标题" prop="title">
          <el-input v-model="form.title" placeholder="请输入商品标题" />
        </el-form-item>
        
        <el-form-item label="商品描述" prop="description">
          <el-input
            v-model="form.description"
            type="textarea"
            :rows="4"
            placeholder="请输入商品描述"
          />
        </el-form-item>
        
        <el-form-item label="商品价格" prop="price">
          <el-input-number
            v-model="form.price"
            :min="0.01"
            :precision="2"
            :step="0.01"
            controls-position="right"
          />
          <span class="price-tip">单位：元</span>
        </el-form-item>
        
        <!-- 商品图片 -->
        <el-form-item 
          label="商品图片" 
          prop="category_id"
          :rules="[{ required: true, message: '请选择图片分类', trigger: 'change' }]"
        >
          <el-select v-model="form.category_id" placeholder="请选择图片分类" @change="handleCategoryChange">
            <el-option 
              v-for="category in categories" 
              :key="category.id" 
              :label="`${category.name} (${category.image_count}张)`" 
              :value="category.id">
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="图片数量" prop="image_count">
          <el-input-number 
            v-model="form.image_count" 
            :min="1" 
            :max="9" 
            :step="1" 
            controls-position="right">
          </el-input-number>
          <span class="price-tip">（1-9张随机图片）</span>
        </el-form-item>

        <el-form-item label="预览图片" v-if="previewImages.length > 0">
          <div class="preview-images">
            <div v-for="(image, index) in previewImages" :key="index" class="preview-image-item">
              <el-image 
                :src="image.url" 
                fit="cover"
                :preview-src-list="previewImages.map(img => img.url)"
                :initial-index="index"
              >
              </el-image>
              <div class="image-info">{{ index === 0 ? '主图' : `图片${index+1}` }}</div>
            </div>
          </div>
          <el-button type="primary" size="small" @click="refreshRandomImages">重新随机</el-button>
        </el-form-item>
        
        <!-- 商品服务 -->
        <el-form-item label="商品服务">
          <el-checkbox-group v-model="form.services">
            <el-checkbox label="SDR">七天无理由退货</el-checkbox>
            <el-checkbox label="NFR">描述不符包邮退</el-checkbox>
            <el-checkbox label="VNR">描述不符全额退（虚拟类）</el-checkbox>
            <el-checkbox label="FD_10MS">10分钟极速发货（虚拟类）</el-checkbox>
            <el-checkbox label="FD_24HS">24小时极速发货</el-checkbox>
            <el-checkbox label="FD_48HS">48小时极速发货</el-checkbox>
            <el-checkbox label="FD_GPA">正品保障</el-checkbox>
          </el-checkbox-group>
          <div class="service-tips">
            <p>可多选，不选则不提供任何服务</p>
          </div>
        </el-form-item>

        <!-- 上架时间设置 -->
        <el-form-item label="上架时间设置">
          <el-radio-group v-model="publishTimeType">
            <el-radio :value="'now'">立即上架</el-radio>
            <el-radio :value="'schedule'">定时上架</el-radio>
          </el-radio-group>
        </el-form-item>

        <!-- 定时上架时间 -->
        <el-form-item 
          label="定时上架时间" 
          prop="specify_publish_time"
          v-if="publishTimeType === 'schedule'"
        >
          <el-date-picker
            v-model="form.specify_publish_time"
            type="datetime"
            placeholder="选择定时上架时间"
            format="YYYY-MM-DD HH:mm:ss"
            value-format="YYYY-MM-DD HH:mm:ss"
            :disabled-date="disabledDate"
            :disabled-time="disabledTime"
          />
        </el-form-item>
        
        <!-- 回调通知URL -->
        <el-form-item label="回调通知URL" prop="notify_url">
          <el-input 
            v-model="form.notify_url" 
            placeholder="请输入回调通知URL（可选）" 
          />
          <div class="url-tips">
            <p>如需接收上架结果通知，请填写回调URL</p>
          </div>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="submitForm">发布商品</el-button>
          <el-button @click="resetForm">重置</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import { ref, reactive, onMounted, computed } from 'vue'
import { ElMessage, ElMessageBox } from 'element-plus'
import { useRouter } from 'vue-router'
import { useUserStore } from '@/stores/user'
import CozeWorkflowTester from '../components/CozeWorkflowTester.vue'

export default {
  name: 'ProductPublish',
  components: {
    CozeWorkflowTester
  },
  setup() {
    const router = useRouter()
    const userStore = useUserStore()
    
    const formRef = ref(null)
    const loading = ref(false)
    const shops = ref([])
    const authorizedShop = ref(null)
    const categories = ref([])
    const previewImages = ref([])
    const publishTimeType = ref('now') // 上架时间类型
    
    // Coze API设置
    const apiSettings = reactive({
      cozeApiToken: '',
      workflowId: '',
      botId: ''
    })
    
    // 判断是否有API设置
    const hasApiSettings = computed(() => {
      return apiSettings.cozeApiToken && apiSettings.workflowId;
    })
    
    // 从全局设置中加载API设置
    const loadCozeSettings = () => {
      try {
        const savedSettings = localStorage.getItem('apiSettings')
        if (savedSettings) {
          const parsed = JSON.parse(savedSettings)
          
          // 映射保存的设置到正确的字段名
          if (parsed.cozeApiToken) {
            apiSettings.cozeApiToken = parsed.cozeApiToken
          }
          
          if (parsed.cozeWorkflowId) {
            apiSettings.workflowId = parsed.cozeWorkflowId
          }
          
          if (parsed.cozeBotId) {
            apiSettings.botId = parsed.cozeBotId
          }
        }
      } catch (error) {
        console.error('加载API设置失败:', error)
      }
    }
    
    // 跳转到设置页面
    const goToSettings = () => {
      router.push({
        path: '/settings',
        query: { tab: 'api' }
      })
    }
    
    // 处理工作流执行结果
    const handleWorkflowResult = (result) => {
      if (result && result.isCompleted && result.parsedOutput) {
        try {
          // 分割输出内容
          const lines = result.parsedOutput.trim().split('\n')
          
          if (lines.length > 0) {
            // 第一行作为标题
            form.title = lines[0].trim()
            
            // 其余行作为描述
            if (lines.length > 1) {
              form.description = lines.slice(1).join('\n').trim()
            }
            
            ElMessage.success('已成功填充商品信息')
          }
        } catch (error) {
          console.error('处理工作流结果失败:', error)
          ElMessage.error('处理工作流结果失败')
        }
      }
    }
    
    // 表单数据
    const form = reactive({
      shop_id: '',
      title: '',
      description: '',
      price: 9.9,
      images: [],
      category_id: '',
      image_count: 3, // 默认3张图片
      specify_publish_time: '', // 定时上架时间
      notify_url: '',  // 回调通知URL
      services: [] // 商品服务
    })
    
    // 表单验证规则
    const rules = {
      shop_id: [
        { required: true, message: '请选择店铺', trigger: 'change' }
      ],
      title: [
        { required: true, message: '请输入商品标题', trigger: 'blur' },
        { min: 3, max: 60, message: '标题长度在3到60个字符之间', trigger: 'blur' }
      ],
      description: [
        { required: true, message: '请输入商品描述', trigger: 'blur' },
        { min: 5, max: 5000, message: '描述长度在5到5000个字符之间', trigger: 'blur' }
      ],
      price: [
        { required: true, message: '请输入商品价格', trigger: 'blur' },
        { type: 'number', min: 0.01, message: '价格必须大于0', trigger: 'blur' }
      ],
      category_id: [
        { required: true, message: '请选择图片分类', trigger: 'change' }
      ],
      specify_publish_time: [
        { required: true, message: '请选择定时上架时间', trigger: 'change' }
      ],
      notify_url: [
        { pattern: /^(https?:\/\/)?([\w.-]+)\.([a-zA-Z]{2,})(:\d+)?(\/\S*)?$/, message: '请输入有效的URL地址', trigger: 'blur' }
      ]
    }

    // 禁用过去的日期
    const disabledDate = (time) => {
      return time.getTime() < Date.now() - 8.64e7; // 禁用过去的日期
    }
    
    // 禁用的时间段
    const disabledTime = () => {
      const now = new Date();
      
      // 如果选择的是当天，则禁用过去的时间
      return {
        disabledHours: () => {
          const hours = [];
          if (form.specify_publish_time && form.specify_publish_time.split(' ')[0] === now.toISOString().split('T')[0]) {
            for (let i = 0; i < now.getHours(); i++) {
              hours.push(i);
            }
          }
          return hours;
        }
      };
    }

    // 获取店铺列表
    const fetchShops = async () => {
      try {
        loading.value = true
        const response = await fetch('/api/shops', {
          method: 'GET',
          headers: {
            'Authorization': `Bearer ${userStore.token}`
          }
        })
        
        const result = await response.json()
        
        if (!result.error && result.data) {
          shops.value = result.data
          
          // 如果只有一个店铺，自动选择
          if (shops.value.length === 1) {
            form.shop_id = shops.value[0].id
            await fetchShopAuthorize()
          }
        } else {
          ElMessage.error(`获取店铺列表失败: ${result.message || '未知错误'}`)
        }
      } catch (error) {
        console.error('获取店铺列表异常:', error)
        ElMessage.error('网络异常，请稍后重试')
      } finally {
        loading.value = false
      }
    }

    // 获取图片分类
    const fetchCategories = async () => {
      try {
        loading.value = true
        const response = await fetch('/api/image-categories', {
          headers: {
            'Authorization': `Bearer ${userStore.token}`
          }
        })
        
        if (response.ok) {
          const result = await response.json()
          if (!result.error && result.data) {
            categories.value = result.data
          } else {
            ElMessage.error('获取图片分类失败')
          }
        } else {
          ElMessage.error('获取图片分类失败')
        }
      } catch (error) {
        console.error('获取图片分类异常:', error)
        ElMessage.error('网络异常，请稍后重试')
      } finally {
        loading.value = false
      }
    }
    
    // 分类变化时获取随机图片预览
    const handleCategoryChange = async () => {
      if (form.category_id) {
        // 先检查分类中是否有图片
        try {
          // 直接使用图片列表API来检查分类中的图片数量
          const response = await fetch(`/api/images?category_id=${form.category_id}&limit=1`, {
            headers: {
              'Authorization': `Bearer ${userStore.token}`
            }
          })
          
          if (response.ok) {
            const result = await response.json()
            console.log('分类图片检查:', result)
            
            if (result.error === false && result.data && result.data.images && result.data.images.length > 0) {
              // 有图片，获取随机图片
              fetchRandomImages()
            } else {
              ElMessage.warning(`该分类下没有图片，请先上传图片或选择其他分类`)
              previewImages.value = []
              form.images = []
            }
          } else {
            console.error('检查分类图片失败:', await response.text())
            // 继续尝试获取随机图片
            fetchRandomImages()
          }
        } catch (error) {
          console.error('检查分类图片数量异常:', error)
          // 出错时仍然尝试获取随机图片
          fetchRandomImages()
        }
      } else {
        previewImages.value = []
      }
    }
    
    // 获取随机图片
    const fetchRandomImages = async () => {
      if (!form.category_id) return
      
      try {
        loading.value = true
        const response = await fetch(`/api/images/random?category_id=${form.category_id}&count=${form.image_count}`, {
          headers: {
            'Authorization': `Bearer ${userStore.token}`
          }
        })
        
        if (response.ok) {
          const result = await response.json()
          console.log('随机图片API响应:', result)
          if (!result.error && result.data) {
            previewImages.value = result.data
            // 更新表单中的图片URL数组
            form.images = result.data.map(img => img.url)
          } else {
            ElMessage.error(`获取随机图片失败: ${result.message || '未知错误'}`)
            console.error('获取随机图片失败:', result)
            previewImages.value = []
            form.images = []
          }
        } else {
          const errorText = await response.text()
          console.error('获取随机图片HTTP错误:', response.status, errorText)
          ElMessage.error(`获取随机图片失败: ${response.status} ${response.statusText}`)
          previewImages.value = []
          form.images = []
        }
      } catch (error) {
        console.error('获取随机图片异常:', error)
        ElMessage.error('网络异常，请稍后重试')
        previewImages.value = []
        form.images = []
      } finally {
        loading.value = false
      }
    }
    
    // 刷新随机图片
    const refreshRandomImages = () => {
      fetchRandomImages()
    }
    
    // 获取店铺授权信息
    const fetchShopAuthorize = async () => {
      if (!form.shop_id) return
      
      try {
        loading.value = true
        
        // 获取选中的店铺信息
        const shopResponse = await fetch(`/api/shops/${form.shop_id}`, {
          method: 'GET',
          headers: {
            'Authorization': `Bearer ${userStore.token}`
          }
        })
        
        const shopResult = await shopResponse.json()
        
        if (shopResult.error || !shopResult.data) {
          ElMessage.error(`获取店铺信息失败: ${shopResult.message || '未知错误'}`)
          return
        }
        
        // 店铺信息获取成功，改用后端API获取授权信息，而不是直接调用闲鱼API
        const response = await fetch(`/api/shops/${form.shop_id}/query`, {
          method: 'POST',
          headers: {
            'Authorization': `Bearer ${userStore.token}`,
            'Content-Type': 'application/json'
          }
        })
        
        const result = await response.json()
        
        if (result.error || !result.data || !result.data.shops || result.data.shops.length === 0) {
          authorizedShop.value = null
          ElMessage.error(`获取店铺授权信息失败: ${result.message || '未知错误'}`)
        } else {
          authorizedShop.value = result.data.shops[0]
          ElMessage.success(`已连接到授权店铺: ${authorizedShop.value.user_name}`)
        }
      } catch (error) {
        console.error('获取店铺授权信息异常:', error)
        ElMessage.error(`网络异常: ${error.message}`)
        authorizedShop.value = null
      } finally {
        loading.value = false
      }
    }
    
    // 提交表单
    const submitForm = async () => {
      if (!formRef.value) return
      
      await formRef.value.validate(async (valid) => {
        if (valid) {
          // 检查是否有图片
          if (form.images.length === 0) {
            // 尝试获取随机图片
            if (form.category_id) {
              await fetchRandomImages()
              // 再次检查是否获取到了图片
              if (form.images.length === 0) {
                ElMessage.error('无法获取图片，请选择其他分类')
                return
              }
            } else {
              ElMessage.error('请先选择图片分类')
              return
            }
          }
          
          // 检查是否有授权店铺信息
          if (!authorizedShop.value) {
            ElMessage.error('请先选择并连接授权店铺')
            return
          }
          
          try {
            loading.value = true
            
            // 构建请求体
            const productData = {
              shopId: form.shop_id,
              title: form.title,
              description: form.description,
              price: form.price,
              images: form.images,
              services: form.services,
              publishOptions: {
                specifyPublishTime: publishTimeType.value === 'schedule' ? form.specify_publish_time : null,
                notifyUrl: form.notify_url || null
              }
            }
            
            // 调用后端API创建商品
            const response = await fetch('/api/products', {
              method: 'POST',
              headers: {
                'Authorization': `Bearer ${userStore.token}`,
                'Content-Type': 'application/json'
              },
              body: JSON.stringify(productData)
            })
            
            const result = await response.json()
            
            if (result.error) {
              throw new Error(`创建商品失败: ${result.message || '未知错误'}`)
            }
            
            const productId = result.data.productId
            
            ElMessage.success(`商品创建成功! 商品ID: ${productId}`)
            
            // 创建成功后询问是否立即发布
            const confirmPublish = await ElMessageBox.confirm(
              `商品创建成功，商品ID: ${productId}，是否立即发布?`,
              '发布确认',
              {
                confirmButtonText: '立即发布',
                cancelButtonText: '稍后发布',
                type: 'success'
              }
            ).catch(() => false)
            
            if (confirmPublish) {
              // 调用后端API发布商品
              const publishResponse = await fetch(`/api/products/${productId}/publish`, {
                method: 'POST',
                headers: {
                  'Authorization': `Bearer ${userStore.token}`,
                  'Content-Type': 'application/json'
                },
                body: JSON.stringify({
                  specifyPublishTime: publishTimeType.value === 'schedule' ? form.specify_publish_time : null,
                  notifyUrl: form.notify_url || null
                })
              })
              
              const publishResult = await publishResponse.json()
              
              if (publishResult.error) {
                ElMessage.error(`发布失败: ${publishResult.message || '未知错误'}`)
                return
              }
              
              if (publishTimeType.value === 'schedule') {
                ElMessage.success(`商品已设置定时上架，上架时间: ${form.specify_publish_time}`)
              } else {
                ElMessage.success('商品发布成功')
              }
              router.push('/publish-plans')
            } else {
              // 不发布，跳转到发布计划页面
              router.push('/publish-plans')
            }
          } catch (error) {
            console.error('创建商品异常:', error)
            ElMessage.error(`网络异常: ${error.message}`)
          } finally {
            loading.value = false
          }
        } else {
          ElMessage.error('请完善表单信息')
        }
      })
    }
    
    // 重置表单
    const resetForm = () => {
      if (formRef.value) {
        formRef.value.resetFields()
        form.images = []
        form.services = []
        previewImages.value = []
        publishTimeType.value = 'now'
      }
    }
    
    // 页面加载时获取店铺列表和图片分类
    onMounted(() => {
      fetchShops()
      fetchCategories()
      loadCozeSettings() // 加载Coze API设置
    })
    
    return {
      formRef,
      form,
      rules,
      loading,
      shops,
      authorizedShop,
      categories,
      previewImages,
      publishTimeType,
      disabledDate,
      disabledTime,
      submitForm,
      resetForm,
      fetchShopAuthorize,
      handleCategoryChange,
      refreshRandomImages,
      apiSettings, // 暴露Coze API设置
      hasApiSettings, // 暴露Coze API设置状态
      handleWorkflowResult, // 暴露处理工作流结果的方法
      goToSettings // 暴露跳转到设置页面的方法
    }
  }
}
</script>

<style scoped>
.product-publish-container {
  max-width: 900px;
  margin: 0 auto;
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.coze-workflow-section {
  margin-bottom: 20px;
}

.price-tip {
  margin-left: 10px;
  color: #909399;
  font-size: 14px;
}

.image-tips, .url-tips, .service-tips {
  margin-top: 10px;
  color: #909399;
  font-size: 14px;
}

